
body {
  --main-font-color: rgb(0, 216, 216);
  background-color: rgb(77, 77, 77);
  color: var(--main-font-color);
}
.fcc_img{
  max-width:50px;
}
.title{
  font-size: 1.5em;
  font-family: 'Lato',sans-serif;
}

.navbar{
  box-shadow: 0px 3px 8px var(--main-font-color);
}

.card{
  margin-left:auto;
  margin-right:auto;
  text-align: center;
  width:400px;
  top:20vh;
  border-radius: 20px;
  
}

.drum-pad{
  width:80px;
  height:80px;
  margin:.25em;
  border-radius: 20px;
}

.bank-pad{
  width:80px;
  height:80px;
  margin:.25em;
  border-radius: 20px;
  font-size: 1em;
  font-family: 'Lato',sans-serif;
}


.btn-secondary:hover{
  color:var(--main-font-color);
}
#display{
  width:200px;
  height:50px;
  background-color:black;
    
  margin:auto;
  padding-top:10px;
  font-size: 1.5em;
  border-radius: 5px;
}
.display-glow{
  box-shadow: inset 0px 0px 25px 5px var(--main-font-color);
}

.glow{
  color:var(--main-font-color);
  text-shadow: 0px 0px 30px var(--main-font-color);
  box-shadow: 0px 3px 15px var(--main-font-color);
}

.glow-text{
  color:var(--main-font-color);
  text-shadow: 0px 0px 30px var(--main-font-color);
}


@media screen and (max-width: 600px) {
  .card {
    width:100%;
    top:30px;
  }
  :root{
    --main-font-color: rgb(216, 0, 0);
  
  }
}